import React from 'react';


const VideoDetail = ({ video }) =>{
if(!video){
    return <div></div>;
}



    const videoSrc = `https://www.youtube.com/embed/${video.id.videoId}`;
        var resp_container = {
                position: 'relative',
                overflow: 'hidden',
                paddingTop: '56.25%'
            
        };
        var resp_iframe = {
            position: 'absolute',
            top: '0',
            left: '0',
            width: '100%',
            height: '100%',
            border: '0'
        
    };

    return (
        <div>
            <div className="ui enbed">
            <div style={resp_container}>
            <iframe style={resp_iframe} title="videoPlayer" src={videoSrc} gesture="media"  allow="encrypted-media" allowfullscreen></iframe>
            </div>     
               
            </div>
            <div className="ui segment">
                <h4 className="ui header">{video.snippet.title}</h4>
                <p>{video.snippet.description}</p>
            </div>    
        </div>
    );

   
};
export default VideoDetail;