import React from 'react';

class Searchbar extends React.Component {
    state = {
        term: ''
    };
    handleChange = (event) => {
        this.setState({
            term: event.target.value
        });
    };
    handleSubmit = event => {
        event.preventDefault();
        this.props.handleFormSubmit(this.state.term);
    }

    render() {
        return (
            <div className='search-bar ui segment'>
                <form onSubmit={this.handleSubmit} className='ui form'>
                    <div className='field'>
                        <label htmlFor="video-search">Video Search By Yaron Cohen
                        
                        </label>
                        <input onChange={this.handleChange} name='video-search' type="text" value={this.state.term}/>
                        <div className="ui two column centered grid">
                            
                            <button onClick={this.handleSubmit} style={{margin:'25px 0px 10px 0px' ,padding:'10px 50px'}} className="ui button">Search</button>
                            
                        </div>
                    </div>
                </form>
            </div>
        )
    }
}
export default Searchbar;